@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.taskList{
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    scrollbar-width: thin;
    height: 55vh;
    border: 10px solid rgb(105, 105, 105);
    border-radius: 10px;
    padding: 10px 20px;
    overflow-x: hidden;
}

.subTitle{
    font-size: 2vw;
    font-family: 'Montserrat', sans-serif;
}

@media (max-width: 900px){
    .taskList{
        height: 50vh;
        overflow-x: hidden;
        overflow-y: scroll;
    }
}
html{
    overflow-x:hidden;
}

.card-container{
    display: flex;
    

    border-radius: 10px;
    overflow-x: hidden;
    box-shadow: 0px 0px 15px -5px;
    padding: 10px 10px;
    margin: 10px 0px; /*updown, leftright*/
    transition: 0.2s;
    -webkit-animation: ease-in;
            animation: ease-in;
}

.card-container:hover{
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
    box-shadow: 0px 0px 15px 0px;
}

.card-content{
    width:83.5%;
    margin: 0px 2.5px;
}

.card-title{
    margin-bottom: 0.1rem;
}

h3, p{
    margin: 0;
    padding: 0;
}

.btn button{

    /*background-color: */
    /*border: none*/
}

a{
    font-weight: bold;
}

html{
    overflow-x:hidden;
}

.title{
    font-size: 7vw;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
}

.header{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    height: 35vh;
}

@media (max-width: 900px){
    .header{
        justify-content: space-evenly;
        height: 25vh;
    }
    .title{
        font-size: 5vw;
    }
}

.inputs{
    width: 80vw;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    padding: 10px 20px;
}
